export const LIBRARY_NAME = "winlet";

export type WindowState = "normal" | "minimized" | "maximized";
/**
 * 
 * - `none`: 
 * - `frozen`: 
 * - `suspended`: DOM
 * - `unloaded`: 
 */
export type VirtualizationLevel = "none" | "frozen" | "suspended" | "unloaded";

/**
 * 
 * - string: DOM
 * - HTMLElement: DOM
 * - MouseEvent: 
 * - object:  { x: number; y: number }
 */
export type AnimationOrigin = string | HTMLElement | MouseEvent | { x: number; y: number };

/**
 * 
 */
export interface AnimationOptions {
	origin?: AnimationOrigin;
}

export interface WindowContentOptions {
	/**
	 * HTML
	 */
	html?: string;
	/**
	 * iframe
	 */
	iframe?: {
		src?: HTMLIFrameElement["src"];
		srcdoc?: HTMLIFrameElement["srcdoc"];
		allow?: HTMLIFrameElement["allow"];
		referrerPolicy?: HTMLIFrameElement["referrerPolicy"];
		loading?: HTMLIFrameElement["loading"];
		sandbox?: string[] | string;
		/**
		 * srcdociframeWinLet
		 * trueGlobalConfigOptionslibraryPath
		 */
		loadWinLet?: boolean;
	};
	/**
	 * HTMLselector
	 */
	template?: string;
}

/**
 * 
 */
export interface SplitViewPane {
	id: string;
	content: WindowContentOptions;
	size?: string; // "50%", "200px" 
	minSize?: string;
	resizable?: boolean;
}

/**
 * 
 */
export interface SplitViewOptions {
	direction: "horizontal" | "vertical";
	panes: SplitViewPane[];
}

/**
 * 
 */
export interface StatusBarOptions {
	/**
	 * 
	 * @default false
	 */
	enabled?: boolean;
	/**
	 * 
	 */
	text?: string;
	/**
	 * HTML
	 * @default false
	 */
	allowHTML?: boolean;
}

/**
 * 
 */
export interface SearchOptions {
	/**
	 * UI
	 * @default true
	 */
	enabled?: boolean;
	/**
	 * 
	 * @default false
	 */
	caseSensitive?: boolean;
	/**
	 * /
	 * @default true
	 */
	showCaseSensitiveButton?: boolean;
	/**
	 * 
	 * @default true
	 */
	showRegexButton?: boolean;
	/**
	 * 
	 * @default true
	 */
	showWholeWordButton?: boolean;
	/**
	 * 
	 * 
	 */
	targetSelector?: string;
}

export interface ListenerOptions {
	once?: boolean;
}

export interface WindowEventMap {
	open: (win: IWindow) => void;
	close: (win: IWindow) => void;
	"before-close": (win: IWindow) => boolean | void;
	focus: (win: IWindow) => void;
	blur: (win: IWindow) => void;
	"move-start": (win: IWindow) => void;
	"move-end": (win: IWindow) => void;
	move: (win: IWindow) => void;
	"resize-start": (win: IWindow) => void;
	"resize-end": (win: IWindow) => void;
	resize: (win: IWindow) => void;
	reload: (win: IWindow) => boolean | void;
	[key: string]: (...args: any[]) => any;
}

export interface GlobalEventMap {
	"window-created": (win: IWindow) => void;
	"window-destroyed": (win: IWindow) => void;
	[key: string]: (...args: any[]) => any;
}

// IWindow
export interface IWindow {
	readonly id: string;
	options: Required<WindowOptions>;
	el: HTMLElement;
	state: WindowState;

	on<K extends keyof WindowEventMap>(eventName: K, listener: WindowEventMap[K], options?: ListenerOptions): void;
	off<K extends keyof WindowEventMap>(eventName: K, listener: WindowEventMap[K]): void;
	emit<K extends keyof WindowEventMap>(eventName: K, ...args: Parameters<WindowEventMap[K]>): ReturnType<WindowEventMap[K]>[] | undefined;

	// --- Public API ---
	/**
	 * 
	 */
	close(): void;
	/**
	 * 
	 * @param options - 
	 */
	minimize(options?: AnimationOptions): void;
	/**
	 * /
	 */
	toggleMaximize(): void;
	/**
	 * 
	 */
	maximize(): void;
	/**
	 * 
	 * @param options - 
	 */
	restore(options?: AnimationOptions): void;
	/**
	 * 
	 */
	focus(): void;
	/**
	 * 
	 */
	blur(): void;
	/**
	 * 
	 */
	shake(): void;
	/**
	 * 
	 * @returns PNGURLPromise
	 * @see https://github.com/niklasvh/html2canvas
	 */
	capture(): Promise<string>;
	/**
	 * 
	 */
	print(): void;
	getTitle(): string;
	setTitle(title: string): void;
	setIcon(icon: string | null): void;
	getPosition(): { x: number; y: number };
	setPosition(x: number | "center" | "left" | "right" | "auto", y: number | "center" | "top" | "bottom" | "auto"): void;
	getSize(): { width: number; height: number };
	setSize(width: number | string, height: number | string): void;
	/**
	 * 
	 * @param opacity 0.0 ()  1.0 () 
	 */
	setOpacity(opacity: number): void;
	/**
	 * 
	 */
	reload(): void;

	/**
	 * iframe
	 * @returns HTMLElementiframeHTMLIFrameElement
	 */
	getContent(): HTMLElement | HTMLIFrameElement;

	/**
	 * 
	 * @param text - HTML
	 */
	setStatusBarText(text: string): void;

	/**
	 * UI
	 */
	openSearch(): void;

	/**
	 * UI
	 */
	closeSearch(): void;

	/**
	 * 
	 * @param options - 
	 * @returns IWindow
	 */
	createWindow(options?: WindowOptions): IWindow;

	/**
	 * 
	 * @param options - 
	 * @returns IWindow
	 */
	createPopup(options: PopupOptions): IWindow;
	/**
	 * 
	 * @param options - 
	 * @returns Promise
	 */
	createAsyncPopup(options: PopupOptions): Promise<PopupResult>;

	/**
	 * 
	 *
	 * ()
	 *
	 * @param options - 
	 */
	setOptions(options: Partial<WindowOptions>): void;
}

export interface MenuItem {
	name?: string;
	action?: (win: IWindow) => void;
	items?: MenuItem[];
	separator?: boolean;
	shortcut?: string;
}

export interface TabItem {
	title: string;
	content: WindowContentOptions;
}

export interface ContextMenuItem {
	name?: string;
	action?: (win: IWindow) => void;
	separator?: boolean;
}

export interface CustomControlButton {
	/**
	 * 
	 */
	name: string;
	/**
	 * HTML (: '<i class="fa fa-question"></i>')
	 */
	html: string;
	/**
	 * 
	 */
	title?: string;
	/**
	 * 
	 */
	action: (win: IWindow) => void;
}

export interface WindowOptions {
	/**
	 * ID
	 */
	id?: string | null;
	/**
	 * 
	 *
	 * focus (): 
	 * prevent: 
	 * create: ID
	 */
	ifExists?: "focus" | "prevent" | "create";
	/**
	 * 
	 */
	title?: string;
	/**
	 * 
	 *
	 * url / font-awesome
	 */
	icon?: string | null;
	/**
	 * X
	 */
	x?: number | "center" | "left" | "right" | "auto";
	/**
	 * Y
	 */
	y?: number | "center" | "top" | "bottom" | "auto";
	/**
	 * X
	 */
	width?: number;
	/**
	 * Y
	 */
	height?: number;
	/**
	 * X
	 */
	minWidth?: number;
	/**
	 * Y
	 */
	minHeight?: number;
	/**
	 * 
	 * `enableVirtualization` true
	 * @default true
	 */
	virtualizable?: boolean;
	/**
	 * 
	 * -  (e.g., '#my-button')
	 * - HTMLElement
	 * - MouseEvent
	 * -  {x: number, y: number}
	 */
	animationOrigin?: AnimationOrigin | null;
	/**
	 * 
	 */
	windowOptions?: {
		/**
		 * 
		 */
		resizableX?: boolean;
		/**
		 * 
		 */
		resizableY?: boolean;
		/**
		 * 
		 */
		movable?: boolean;
		/**
		 * 
		 */
		closable?: boolean;
		/**
		 * 
		 */
		minimizable?: boolean;
		/**
		 * 
		 */
		maximizable?: boolean;
		/**
		 * 
		 */
		maximizableOnDblClick?: boolean;
		/**
		 * 
		 */
		alwaysOnTop?: boolean;
		/**
		 * 
		 */
		useGhostWindow?: boolean;
		/**
		 * 
		 */
		focus?: boolean;
		/**
		 *  (0.0 - 1.0)
		 */
		opacity?: number;
		/**
		 * 
		 * true
		 */
		modal?: boolean;
	};
	/**
	 * 
	 */
	content?: WindowContentOptions;
	/**
	 * 
	 * `content`
	 */
	splitView?: SplitViewOptions;
	/**
	 * 
	 */
	statusBar?: StatusBarOptions;
	/**
	 * 
	 */
	search?: SearchOptions;
	/**
	 * 
	 */
	menu?: MenuItem[];
	/**
	 * 
	 * - default: 
	 * - merged: 
	 */
	menuStyle?: "default" | "merged";
	/**
	 * 
	 */
	tabs?: TabItem[];
	/**
	 * 
	 * - default: 
	 * - merged: Chrome
	 */
	tabStyle?: "default" | "merged";
	tabOptions?: {
		/**
		 * 
		 */
		reorderable?: boolean;
		/**
		 * 
		 */
		closable?: boolean;
		/**
		 * 
		 */
		addable?: boolean;
		/**
		 * 
		 */
		detachable?: boolean;
		/**
		 * 
		 */
		mergeable?: boolean;
		/**
		 * 
		 */
		allowIncomingMerge?: boolean;
		/**
		 * 
		 */
		onAdd?: (win: IWindow) => TabItem;
		/**
		 * 
		 * @param sourceWindow 
		 * @param targetWindow 
		 * @returns true: false:
		 */
		onMergeAttempt?: (sourceWindow: IWindow, targetWindow: IWindow) => boolean;
	};
	/**
	 * 
	 */
	taskbarOptions?: {
		/**
		 * true
		 * 
		 */
		showIconOnly?: boolean;
	};
	/**
	 * 
	 */
	contextMenu?: ContextMenuItem[];
	/**
	 * 
	 */
	customControls?: CustomControlButton[];
	/**
	 * 
	 */
	enableShortcuts?: boolean;
	/**
	 * 
	 */
	controlsPosition?: "left" | "right";
	/**
	 * iframe
	 */
	showLoadingIndicator?: boolean;
	/**
	 * 
	 * @default false
	 */
	lazyLoad?: boolean;
	/**
	 * move
	 * @default false
	 */
	disableMoveEvent?: boolean;
	/**
	 * resize
	 * @default false
	 */
	disableResizeEvent?: boolean;
	/**
	 * 
	 * 'standard': DOM/ ()
	 * 'canvas': CanvasDOM
	 */
	virtualizationStrategy?: "standard" | "canvas" | null;
	/**
	 * 
	 * 'auto':  ()
	 * 'manual': 
	 */
	virtualizationRestoreMode?: "auto" | "manual";
	/**
	 * 
	 * @default true
	 */
	showVirtualizationRefreshButton?: boolean;
	/**
	 * 
	 */
	onOpen?: (win: IWindow) => void;
	/**
	 * 
	 */
	onClose?: (win: IWindow) => void;
	/**
	 * false
	 */
	onBeforeClose?: (win: IWindow) => boolean | void;
	/**
	 * 
	 */
	onFocus?: (win: IWindow) => void;
	/**
	 * 
	 */
	onBlur?: (win: IWindow) => void;
	/**
	 * 
	 */
	onResize?: (win: IWindow) => void;
	/**
	 * 
	 */
	onMoveStart?: (win: IWindow) => void;
	/**
	 * 
	 */
	onMoveEnd?: (win: IWindow) => void;
	/**
	 * 
	 */
	onResizeStart?: (win: IWindow) => void;
	/**
	 * 
	 */
	onResizeEnd?: (win: IWindow) => void;
	/**
	 * 
	 */
	onMove?: (win: IWindow) => void;
	/**
	 * 
	 *
	 * iframehtml `false` 
	 */
	onReload?: (win: IWindow) => boolean | void;

	// 
	_isPopup?: boolean;
	/**
	 *  ()
	 */
	_parent?: IWindow | null;
	/**
	 *  ()
	 */
	_taskbarItem?: HTMLElement | null;
}

// --- Popup ---
export const TIMEOUT_RESULT = Symbol("timeout");
export const CLOSE_BUTTON_RESULT = Symbol("close");

export type PopupResult = string | number | symbol | null;

export interface PopupButton {
	text: string;
	value: PopupResult;
}

export type PopupButtonPreset = "Ok" | "OkCancel" | "Yes" | "YesNo" | "YesNoCancel" | "Retry" | "RetryCancel" | "AbortRetryIgnore";

export interface PopupOptions {
	message: string;
	title?: string;
	icon?: string | null;
	buttons?: PopupButton[] | PopupButtonPreset;
	timeout?: number;
	autoWidth?: boolean;
	focus?: boolean;
	alwaysOnTop?: boolean;
	modal?: boolean;

	/**
	 * 
	 */
	onClose?: (result: PopupResult) => void;
	/**
	 * 
	 */
	onFocus?: (win: IWindow) => void;
	/**
	 * 
	 */
	onBlur?: (win: IWindow) => void;
}

export interface TaskbarOptions {
	/**
	 * 
	 * @default 'bottom'
	 */
	position?: "top" | "bottom" | "left" | "right";
}

export interface GlobalConfigOptions {
	windowSwitchShortcut?: string | null; // : "Ctrl+`"
	/**
	 * 
	 * HTMLElement
	 * body
	 */
	container?: string | HTMLElement;
	/**
	 * `srcdoc` iframeWinLet
	 */
	libraryPath?: string;
	/**
	 * 
	 * @default true
	 */
	enableAnimations?: boolean;
	/**
	 * 
	 * @default true
	 */
	animateFromTaskbar?: boolean;
	/**
	 * 
	 * @default false
	 */
	enableTaskbar?: boolean;
	/**
	 * 
	 */
	taskbar?: TaskbarOptions;
	/**
	 * 
	 * @default true
	 */
	enableFocusTrapping?: boolean;
	/**
	 * 
	 * 
	 * @default true
	 */
	enableVirtualization?: boolean;
	/**
	 * 
	 * @default true
	 */
	indicateVirtualizationInTaskbar?: boolean;
	/**
	 * 
	 * 'standard': DOM/ ()
	 * 'canvas': CanvasDOM
	 */
	virtualizationStrategy?: "standard" | "canvas";
	/**
	 * 
	 * 'auto':  ()
	 * 'manual': 
	 */
	virtualizationRestoreMode?: "auto" | "manual";
	/**
	 * 
	 * 
	 * @default 5
	 */
	virtualizationThreshold?: number;
	/**
	 * 
	 * `enableVirtualization` true
	 * @default 10000
	 */
	virtualizationDelay?: number;
	/**
	 *  ()
	 */
	eventThrottling?: {
		moveDelay?: number;
		resizeDelay?: number;
	};
	/**
	 * 
	 * 'dark'Theme
	 * @default 'default'
	 */
	theme?: string | Theme;
	/**
	 * Bootstrap
	 * @default true
	 */
	autoDetectBootstrapTheme?: boolean;
	/**
	 * 
	 * ID
	 * @default false
	 */
	enableDebugMode?: boolean;
}

/**
 * 
 */
export interface Theme {
	name: string;
	variables: Record<string, string>;
}

// WinLetAPI
export interface WinLetApi {
	init: (options?: GlobalConfigOptions) => void;
	on<K extends keyof GlobalEventMap>(eventName: K, listener: GlobalEventMap[K], options?: ListenerOptions): void;
	off<K extends keyof GlobalEventMap>(eventName: K, listener: GlobalEventMap[K]): void;
	emit<K extends keyof GlobalEventMap>(eventName: K, ...args: Parameters<GlobalEventMap[K]>): ReturnType<GlobalEventMap[K]>[] | undefined;
	/**
	 * 
	 * @param options - 
	 * @returns IWindow
	 */
	createWindow: (options?: WindowOptions) => IWindow;
	/**
	 * 
	 * @param options - 
	 * @returns IWindow
	 */
	createPopup: (options: PopupOptions) => IWindow;
	/**
	 * 
	 * @param options - 
	 * @returns Promise
	 */
	createAsyncPopup(options: PopupOptions): Promise<PopupResult>;
	/**
	 * ID
	 * @param id - ID
	 */
	getWindow: (id: string) => IWindow | undefined;
	/**
	 * DOM
	 * 
	 * @param element - DOM
	 * @returns IWindowundefined
	 */
	getWindowFromElement: (element: HTMLElement) => IWindow | undefined;
	/**
	 * 
	 */
	getActiveWindow: () => IWindow | null;
	/**
	 * 
	 * @param win - 
	 * @param state - 
	 */
	updateTaskbarItem: (win: IWindow, state: "minimized" | "restored" | "titleChanged" | "iconChanged" | "virtualized" | "unvirtualized") => void;
	/**
	 * 
	 * @param options - 
	 */
	setDefaultConfig: (options: WindowOptions) => void;
	/**
	 * 
	 * @param options - 
	 */
	setGlobalConfig: (options: GlobalConfigOptions) => void;
	/**
	 * 
	 * @param theme - 
	 */
	registerTheme: (theme: Theme) => void;
	/**
	 * 
	 * @returns 
	 */
	getRegisteredThemes: () => string[];
	/**
	 * 
	 * @param theme - 'default', 'dark'Theme
	 */
	setTheme: (theme: string | Theme) => void;
	/**
	 * 
	 * @returns 
	 */
	getTheme: () => Theme | null;
	/**
	 * WinLet
	 */
	get version(): string;
	POPUP_TIMEOUT_RESULT: typeof TIMEOUT_RESULT;
	POPUP_CLOSE_BUTTON_RESULT: typeof CLOSE_BUTTON_RESULT;
}

// WindowWinLet
declare global {
	interface Window {
		WinLet: WinLetApi;

		// 
		html2canvas?: (element: HTMLElement, options?: any) => Promise<HTMLCanvasElement>;
	}
}
